import React , { useState }  from "react"
import Layout from "../../components/layout"
import SEO from '../../components/seo'
import stl from '../../STYLES/AUTH/sign-in-up.module.scss'
import { useMutation} from '@apollo/react-hooks';
import { SIGN_IN_USER } from '../../queries/index'
import { setCookie } from '../../helpers/index'
import { ErrorAlert } from '../../helpers/index'
import { withAuth } from '../../components/ifAuth'

const initialState = {
    email:null,
    password:null
}


const SignInBody = withAuth(({reFetchObservableQueries}) => {

const [state , setState ] = useState(initialState)

const setStateValue = e => {
    setState({...state , [e.target.name]:e.target.value})
}

const disabled = () => {
    return !state.email || !state.password
}

const [ signInUser , attrs ] = useMutation(SIGN_IN_USER , {
    variables: { email:state.email , password:state.password , lang:"en"},
    onError:(error) => ErrorAlert(error),
    onCompleted:({signInUser}) => {
        setCookie('token' , signInUser.token , 1);
        reFetchObservableQueries()
    }
});

const handleSignIn =  e => {
  e.preventDefault()
  signInUser()
}

    return (
        <div className = {stl.background}>
           <form onSubmit = {(e) => handleSignIn(e)} className = {stl.form}>
               <h1>SIGN IN</h1>
               <input autoComplete="on" name = 'email' onChange = {e => setStateValue(e)} type = 'text' placeholder = 'email or username' />
               <input autoComplete="on" name = 'password' onChange = {e => setStateValue(e)} type = 'password' placeholder = "password" />
               <p>{attrs.loading ? <p>Signing in ...</p> : false}</p>
               <button  disabled = {disabled()} type = 'submit'>Submit</button>
           </form>
           </div>
    )
})

const SignIn = () => (
    <Layout>
        <SEO title = 'sign-in' />
        <SignInBody />
    </Layout>
)

export default SignIn